.info {
  padding: 0 0 60px;
  margin-top: -30px;
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
  }
  &__card {
    background: #FFFFFF;
    box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
    border: 1px solid #ffffff;
    border-radius: 10px;
    padding: 30px 30px 15px;
    text-align: center;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &:hover {
      border: 1px solid #BBEAFE;
      box-shadow: 10px 0px 50px rgba(0, 145, 210, 0.19);
     .info__card-btn {
        background: linear-gradient(89.73deg, #008FD1 0.29%, #5DC6F6 99.83%);
        color: #ffffff;
      }
    }
    &-icon {
      width: 73px;
      height: 73px;
      border-radius: 50%;
      background: #FFFFFF;
      box-shadow: 4px 15px 50px rgba(229, 233, 246, 0.4);
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: -36px;
      right: 15px;
    }
    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 120%;
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #35728E;
      margin-top: 12px;
      margin-bottom: 25px;
    }
    &-btn {
      width: 138px;
      height: 46px;
      background: #FFFFFF;
      border: 1px solid #2FABE4;
      border-radius: 32px;
      font-weight: 600;
      font-size: 16px;
      line-height: 120%;
      color: #35728E;
      margin: 0 auto;
      &2 {
        width: 215px;
      }
      &3 {
        width: 215px;
      }
    }
  }
}
.popular-services {
  padding: 60px 0;
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 12px;
    margin-top: 50px;
  }
  &__card {
    background: #FFFFFF;
    border: 1px solid #ffffff;
    border-radius: 10px;
    filter: drop-shadow(10px 0px 50px rgba(229, 233, 246, 0.4));
    padding: 20px;
    &:hover {
      filter: drop-shadow(10px 0px 50px rgba(0, 145, 210, 0.19));
      border: 1px solid #bbeafe;
    }
    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 120%;
      text-align: center;
    }
    &-img {
      margin-top: 20px;
      img {
        width: 100%;
        height: auto;
        border-radius: 10px;
      }
    }
  }
}
.directions {
  padding: 60px 0;
  position: relative;
  &__img-bg {
    position: absolute;
    z-index: 0;
    top: 130px;
    left: 0;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px 80px;
    margin-top: 50px;
  }
  &__card {
    text-align: center;
    background: #FFFFFF;
    box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
    border: 1px solid #ffffff;
    border-radius: 20px;
    padding: 20px;
    &:hover {
      border: 1px solid #BBEAFE;
      box-shadow: 10px 0px 50px rgba(0, 145, 210, 0.19);
    }
    &-img {
      img {
        width: 100%;
      }
    }
    &-title {
      font-weight: 600;
      font-size: 23px;
      line-height: 120%;
      margin-top: 25px;
    }
  }
}
.about {
  padding: 60px 0 85px;
  &__title {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-top: 30px;
  }
  &__item {
    background: #FFFFFF;
    box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
    border-radius: 50%;
    width: 435px;
    height: 435px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    &2 {
      position: relative;
      z-index: 2;
      margin-left: -53px;
    }
    &3 {
      position: relative;
      z-index: 3;
      margin-left: -53px;
    }
    &-number {
      font-weight: 500;
      font-size: 80px;
      line-height: 100%;
    }
    &-text {
      max-width: 260px;
      font-size: 18px;
      line-height: 120%;
      color: #35728E;
      margin-top: 12px;
    }
  }
}
.connection {
  padding: 85px 0 60px;
  position: relative;
  &__img-bg {
    position: absolute;
    z-index: 0;
    top: -350px;
    right: 0;
    pointer-events: none;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 2;
  }
  &__left {
    max-width: 460px;
    flex-shrink: 0;
  }
  &__right {
    max-width: 560px;
    margin-left: 60px;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
    margin-top: 5px;
    color: #1E1E1E;
  }
  &__form {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 25px;
    &-group {
      padding: 0px;
      margin: 0;
      border: none;
      input {
        width: 100%;
        height: 60px;
        background: #FFFFFF;
        border: 1px solid #D7D7D7;
        box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
        border-radius: 10px;
        padding: 0 15px;
        font-size: 18px;
        line-height: 120%;
        &::placeholder {
          color: #1E1E1E;
          opacity: 0.4;
        }
      }
      textarea {
        width: 100%;
        height: 175px;
        background: #FFFFFF;
        border: 1px solid #D7D7D7;
        box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
        border-radius: 10px;
        padding: 18px 15px;
        font-size: 18px;
        line-height: 120%;
        &::placeholder {
          color: #1E1E1E;
          opacity: 0.4;
        }
      }
    }
    &-check {
      display: flex;
      align-items: center;
      position: relative;
      input {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 2;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label {
          &:after {
            background-image: url("../img/check.svg");
            background-repeat: no-repeat;
            background-position: center;
          }
        }
      }
      label {
        font-size: 14px;
        line-height: 120%;
        letter-spacing: -0.02em;
        padding-left: 36px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 26px;
          height: 26px;
          border: 1px solid #2B2B2B;
          background: transparent;
          border-radius: 4px;
        }
      }
    }
    &-link {
      font-size: 14px;
      line-height: 120%;
      letter-spacing: -0.02em;
      color: #008CC8;
      margin-top: 10px;
      display: block;
    }
  }
}

@media screen and (max-width: 1300px) {
  .about {
    &__items {
      grid-template-columns: 1fr 1fr;
      gap: 30px;
      justify-items: center;
    }
    &__item {
      &2 {
        margin-left: 0;
      }
      &3 {
        margin-left: 0;
        grid-column: 1/3;
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .popular-services {
    &__cards {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
  .directions {
    &__img-bg {
      width: 500px;
      height: auto;
    }
    &__cards {
      gap: 30px;
    }
  }
  .connection {
    &__img-bg {
      width: 500px;
      height: auto;
    }
  }
}

@media screen and (max-width: 992px) {
  .info {
    &__cards {
      grid-template-columns: 1fr;
      gap: 50px;
    }
  }
  .directions {
    &__cards {
      grid-template-columns: 1fr 1fr;
    }
  }
  .about {
    &__items {
      grid-template-columns: 1fr;
      gap: 15px;
    }
    &__item {
      grid-column: 1;
    }
  }
  .connection {
    &__left {
      flex-shrink: unset;
    }
    &__right {
      max-width: 350px;
      margin-left: 30px;
      flex-shrink: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .info {
    padding: 0 0 30px;
    &__cards {
      gap: 30px;
    }
    &__card {
      &-icon {
        top: -25px;
        width: 50px;
        height: 50px;
        svg {
          width: 30px;
          height: auto;
        }
      }
      &-title {
        font-size: 22px;
      }
      &-btn {
        height: 40px;
      }
    }
  }
  .popular-services {
    padding: 30px 0;
    &__cards {
      grid-template-columns: 1fr 1fr;
      margin-top: 30px;
    }
    &__card {
      &-title {
        font-size: 20px;
      }
    }
  }
  .directions {
    padding: 30px 0;
    &__img-bg {
      display: none;
    }
    &__cards {
      margin-top: 30px;
    }
    &__card {
      &-title {
        font-size: 20px;
      }
    }
  }
  .about {
    padding: 30px 0;
    &__item {
      &-number {
        font-size: 50px;
      }
      &-text {
        font-size: 16px;
      }
    }
  }
  .connection {
    padding: 30px 0;
    &__img-bg {
      display: none;
    }
    &__inner {
      flex-direction: column;
      max-width: 450px;
      margin: 0 auto;
    }
    &__left {
      max-width: 100%;
    }
    &__right {
      max-width: 100%;
      margin: 30px 0 0 0;
    }
    &__form {
      gap: 15px;
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 576px) {
  .info {
    &__card {
      padding: 30px 15px 15px;
    }
  }
  .directions {
    &__cards {
      grid-template-columns: 1fr;
      gap: 15px;
    }
  }
  .about {
    &__item {
      width: 380px;
      height: 380px;
    }
  }
}

@media screen and (max-width: 450px) {
  .popular-services {
    &__cards {
      grid-template-columns: 1fr;
    }
  }
  .about {
    &__item {
      width: 280px;
      height: 280px;
      padding: 15px;
    }
  }
}